:root {
	--selected-background: #34495e;
	--selected-color: #fff;
	--disabled-color: #bbb;
	--component-background: #fff;
	--component-color: #424242;
	--underlay-background: rgba(66, 66, 66, 0.3);
	--underlay-background: rgba(66, 66, 66, 0.3);

	--error-color: red;
	--success-color: green;

	--short-animation-duration: .2s;
	--short-animation-easing: ease;

	/* Z-index levels */
	--zindex-base: 0;
	--zindex-tooltip: 100;
	--zindex-dropdown: 200;
	--zindex-fixed: 300;
	--zindex-dialog: 400;
	--zindex-alert: 500;
}

.root {
	display: block;
	position: relative;
}

.input {}

.inputWrapper {}

.checked {}

.focused {}

.disabled {}

.readonly {}

.required {}

/* invalid style */

.invalid .inputWrapper::before {
	border-color: red;
	border-color: var(--error-color);
}

.invalid .inputWrapper::after {
	background-color: red;
	background-color: var(--error-color);
}

/* valid style */

.valid .inputWrapper::before {
	border-color: green;
	border-color: var(--success-color);
}

.valid .inputWrapper::after {
	background-color: green;
	background-color: var(--success-color);
}



